  /*内层轨道，滚动条中间部分（位置4）*/
  display: none;
}
::-webkit-scrollbar-thumb {
  /*滚动条里面可以拖动的那部分（位置5）*/
  border-radius: 4px;
  background-color: #d5d5d5;
  position: absolute;
}
